.library{
    position: fixed ;
    top: 0;
    left: 0;
    width: 20rem;
    height: 100%;
    background: white;
    box-shadow: 2px 2px 5px rgb(204, 204, 204);
    overflow: scroll;
    transform: translateX(-100%) ;
    opacity: 0;
    transition: all .5s ease;
    h2{
        padding: 2rem;
    }
}
.library.library-actived{
    transform: translateX(0%) ;
    opacity: 1;
}
.library-song{
    cursor: pointer;
    display: flex;
    align-items: center;
    padding:  1rem 2rem 1rem 2rem;
    transition: background .4s ease;
    img{
        width: 30%;
    }
    &:hover{
        background: rgb(227, 253, 248);
    }

}
.song-description{
    padding-left: 1rem;
    h3{
        font-size: 1rem;
    }
    h4{
        font-size: 0.7rem ;
    }
}
.actived{
    background: rgb(206, 250, 241);
}

// scrollbar
*{
    scrollbar-width: thin;
    scrollbar-color: rgba(155,155,155, 0.5) transparent;
}
*::-webkit-scrollbar{
    width: 5px;
}
*::-webkit-scrollbar-track{
    background-color: transparent;
}
*::-webkit-scrollbar-thumb{
    background-color:  rgba(155,155,155, 0.5);
    border-radius: 20px;
    border: transparent;
}

@media screen and (max-width: 768px){
    .library{
        width: 100%;
    }
}