.player{
    min-height: 20vh;
    
    display: flex;
    flex-direction: column;
    align-items: center;
}
.time-control{
    width: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    input{
        width: 100%;
        -webkit-appearance: none;   
        cursor: pointer;
        background-color: transparent;
    }
    p{
        padding: 1rem;
    }
}
.play-control{
    width:30%;
    display: flex;
    justify-content: space-between ;
    align-items: center;
    padding: 1rem;
    svg{
        cursor: pointer;
    }
}
input[type='range']:focus{
    outline: none;
}
input[type='range']::-webkit-slider-thumb{
    -webkit-appearance: none;
    height: 16px;
    width: 16px;
    // background: #000;
}
input[type='range']::-moz-slider-thumb{
    -webkit-appearance: none;
    height: 16px;
    width: 16px;
    // background: #000;
}
.track{

    background-color: rgb(100, 179, 224); 
    width: 100%;
    height: 1rem;
    position: relative;
    border-radius: 1rem;
    overflow: hidden;
}
.animate-track{
    background: rgb(204,204,204);
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    
    pointer-events: none;
}
@media screen and (max-width: 768px){
    .track{
        height: 0.7rem;
    }
    .time-control{
        width: 90%;
    }
    .play-control{
    width: 60%;
    }
}